import { Component, h, Host, Prop } from '@stencil/core';
import { NavigationType } from '../../interfaces/components/basic/navigation/navigation';

@Component({
  tag: 'rwc-navigation',
  styleUrl: './navigation.scss',
  shadow: true
})
export class RwcNavigation {
  /**
   * The type of the navigation
   */
  @Prop({ reflect: true }) type: NavigationType = 'horizontal';

  /**
   * Renders the component
   */
  render() {
    return (
      <Host>
        <div class={ {
          container: true,
          [this.type]: true
        } }>
          <slot name="before" />
          <slot />
          <slot name="after" />
        </div>
      </Host>
    );
  }
}
