@import 'global.scss';

:host {
  /**
  * @prop --navigation-container-padding: The padding of the navigation container.
  * @prop --navigation-container-border: The border of the navigation container.
  * @prop --navigation-container-border-radius: The border radius of the navigation container.
  *
  * @prop --navigation-font-family: The font family of the navigation elements.
  * @prop --navigation-font-size: The font size of the navigation elements.
  * @prop --navigation-font-weight: The font weight of the navigation elements.
  * @prop --navigation-line-height: The line height of the navigation elements.
  *
  * @prop display: The display mode of the host element.
  */
  --navigation-container-padding: #{$rwc-spacing--xs};
  --navigation-container-border: none;
  --navigation-container-border-radius: none;
  
  --navigation-font-family: #{$rwc-font-family-default};
  --navigation-font-size: #{$rwc-font-size-body-md};
  --navigation-font-weight: 500;
  --navigation-line-height: #{$rwc-line-height-xs};
  
  display: block;
}

.container {
  display: flex;
  flex-direction: row;
  font-family: var(--navigation-font-family);
  font-size: var(--navigation-font-size);
  font-weight: var(--navigation-font-weight);
  line-height: var(--navigation-line-height);
  padding: var(--navigation-container-padding);
  border: var(--navigation-container-border);
  border-radius: var(--navigation-container-border-radius);

  &.vertical,
  &.accordion {
    flex-direction: column;
  }
}
